.call-to-act a {
  position: relative;
  color: #fff;
  color: var(--type-color);
  text-decoration: none;
  border-color: linear-gradient(50deg, #EE1E26 25%, #F7C514 100%);
  border-color: var(--primary-color);
  background-color: linear-gradient(50deg, #EE1E26 25%, #F7C514 100%);
  background-color: var(--primary-color);
  background: linear-gradient(50deg, #EE1E26 25%, #F7C514 100%);
  background: var(--primary-color);
  padding: 1rem 3rem;
}

.call-to-act a:hover {
  color: #fff;
  color: var(--type-color);
  text-decoration: none;
}

.call-to-act a::before, .call-to-act a::after {
  content: '';
  position: absolute;
  bottom: -0.125rem;
  right: -0.125rem;
  display: inline-block;
  border: 0.25rem solid transparent;
  width: 0.625rem;
  height: 0.625rem;
  z-index: 1;
  border-right-color: #F7C514;
  border-right-color: var(--complement-color);
  border-bottom-color: #F7C514;
  border-bottom-color: var(--complement-color);
  transition: 0.2s 0.4s width, 0.2s 0.2s height, 0.2s 0.2s border-left, 0.2s border-top-color;
}

.call-to-act a:hover::before, .call-to-act a:hover::after {
  border-right-color: #F7C514;
  border-right-color: var(--complement-color);
  border-bottom-color: #F7C514;
  border-bottom-color: var(--complement-color);
  border-left-color: #F7C514;
  border-left-color: var(--complement-color);
  border-top-color: #F7C514;
  border-top-color: var(--complement-color);
  width: calc(100% + 0.25rem);
  height: calc(100% + 0.25rem);
  transition: 0.2s width, 0.2s 0.2s height, 0.2s 0.2s border-left, 0.2s 0.4s border-top-color;
}

.call-to-act .disabled {
  opacity: 0.5;
  cursor: not-allowed;
}

.call-to-act .disabled::before, .call-to-act .disabled::after {
  opacity: 0;
}

